.img {
   border: 1px solid #e4e4e4;
   height: 240px;
}
.img img {
   width: 100%;
   display: block;
}
.info {
   text-align: center;
}
.item {
   margin-top: 20px;
}

.details {
   border: 0;
   background: rgb(81, 170, 81);
   padding: 8px;
   cursor: pointer;
   font-size: 16px;
   text-decoration: none;
   color: #000;
}

.details:hover {
   color: #fff;
   background: rgb(45, 116, 45);
   transition: 0.5s;
}
.text {
   margin: 5px 0px;
   white-space: nowrap;
   text-overflow: ellipsis;
   overflow: hidden;
}
.nav_item {
   margin: 20px 0;
}
