.title {
   font-size: 24px;
   text-align: center;
   font-weight: 400;
}
.sort button {
   font-weight: 400;
   font-size: 16px;
   text-transform: uppercase;
   margin-top: 10px;
}

.sorting:last-child {
   margin-left: 20px;
   border: 1px solid #000;
}

.sorting {
   border: 1px solid #000;
   padding: 5px;
   cursor: pointer;
   background: 0;
}

.sorting:hover {
   border: 1px solid #000;
   background: #86bc42;
   color: #fff;
   transition: 0.5s;
}
.menu {
   display: flex;
   justify-content: space-between;
   align-items: flex-end;
}
.creat {
   border: 1px solid #000;
   padding: 5px;
   cursor: pointer;
   background: 0;
   font-weight: 400;
   font-size: 16px;
   text-transform: uppercase;
   cursor: pointer;
}
.delete {
   border: 1px solid #000;
   padding: 5px;
   cursor: pointer;
   background: 0;
   font-weight: 400;
   font-size: 16px;
   text-transform: uppercase;
   cursor: pointer;
   margin-right: 20px;
}
.creat:hover {
   background: rgb(27, 131, 27);
   transition: 0.5s;
   color: #fff;
}
.delete:hover {
   background: rgb(173, 76, 20);
   transition: 0.5s;
   color: #fff;
}

.items {
   margin-top: 77px;
   display: flex;
   justify-content: space-around;
   flex-wrap: wrap;
}
