.modal {
   height: 100vh;
   width: 100vw;
   background: rgba(0, 0, 0, 0.4);
   position: fixed;
   top: 0;
   left: 0;
   display: flex;
   align-items: center;
   justify-content: center;
}
.content {
   background: #fff;
   height: 40%;
   width: 50%;

   text-align: center;
}
.delete {
   margin-top: 70px;
}
.title {
   margin-bottom: 50px;
   font-size: 24px;
}
.btn_delete {
   margin-left: 10px;
   background: 0;
   border: 1px solid #000;
   padding: 5px;
   cursor: pointer;
   font-weight: 700;
}

.btn_delete:hover {
   background: rgb(131, 15, 15);
   transition: 0.5s;
   color: #fff;
}
.form {
   margin-top: 50px;
   display: flex;
   flex-wrap: wrap;
   justify-content: space-evenly;
}

.form label {
   margin-top: 20px;
}
.form label input {
   margin-left: 10px;
}

.btn_create {
   margin-top: 20px;
   background: 0;
   border: 1px solid #000;
   font-size: 18px;
   font-weight: 700;
   padding: 5px;
   cursor: pointer;
}

.btn_create:hover {
   background: rgb(15, 128, 62);
   color: #fff;
   transition: 0.5s;
}
.edit div {
   margin-top: 5px;
}
.edit input {
   margin-top: 10px;
}

.btns {
   margin-top: 15px !important;
   display: flex;
   justify-content: space-evenly;
}
.btns button {
   padding: 5px;
   font-size: 16px;
   color: #fff;
   border: 0;
   background: rgba(26, 35, 66, 0.904);
   cursor: pointer;
}

.cancel_btn:hover {
   background: rgb(82, 17, 17);
   transition: 0.5s;
}
.edit_btn:hover {
   background: rgb(13, 68, 36);
   transition: 0.5s;
}
