.content {
   background-image: url("https://opencart.dostguru.com/FD01/fruitino_04/image/cache/catalog/slider/Slider2-1920x700.jpg");
   background-repeat: no-repeat;
   height: 100%;
   height: 700px;
}

.info {
   height: 100%;
   display: flex;
   flex-direction: column;
   justify-content: center;
}

.trend {
   font-size: 16px;
   color: #fd6c00;
   text-transform: uppercase;
   font-weight: 400;
}
.title {
   font-size: 50px;
   color: #fff;
   font-weight: 400;
}

.text {
   font-size: 14px;

   color: #fff;
   font-weight: 500;
}
