.img img {
   width: 100%;
}
.img {
   width: 400px;
   height: 500px;
   border: 1px solid #e4e4e4;
}
.detail {
   margin-top: 50px;
   display: flex;
   margin-left: 50px;
}
.info {
   margin-left: 30px;
}
.name {
   font-size: 18px;
   text-transform: capitalize;
   letter-spacing: 1px;
   font-weight: 700;
}
.desc {
   font-size: 17px;
   font-weight: 600;
   font-style: italic;
   margin: 10px 0;
}
.proper {
   text-transform: capitalize;
   font-size: 14px;
   letter-spacing: 1px;
}
.edit {
   margin: 20px 0;
}
.edit button {
   border: 1px solid #000;
   background: rgb(10, 104, 10);
   padding: 10px;
   font-size: 20px;
   cursor: pointer;
}
.edit button:hover {
   background: rgb(50, 119, 50);
   color: #fff;
   cursor: pointer;
   transition: 0.5s;
}
.add_comment {
   margin-top: 60px;
   text-align: center;
}
.btn_comment {
   margin: 30px 0;
}
.btn_comment button {
   padding: 10px;
   border: 1px solid #fff;
   background: rgb(19, 68, 38);
   font-size: 18px;
   cursor: pointer;
   color: rgb(213, 230, 63);
}
.btn_comment button:hover {
   color: #fff;
   background: rgb(68, 158, 33);
   transition: 0.5s;
}

.text {
   font-style: italic;
   font-weight: 600;
   font-size: 18px;
   border: 1px solid rgb(126, 121, 121);
   border-radius: 10px;
   padding: 10px;
}
